.as-contact-form {
  margin-bottom: 2rem; }
  .as-contact-form label {
    padding-bottom: 0.3rem;
    font-size: 1rem; }
  .as-contact-form input[type="text"], .as-contact-form textarea {
    border-width: 3px; }
  .as-contact-form textarea {
    min-height: 5rem; }
  .as-contact-form input[type="submit"] {
    min-width: 5rem;
    border-radius: 8px;
    box-shadow: 0 4px 0 0 #e8af19;
    background-color: #EDBF47;
    color: #FFFFFF;
    font-weight: bold;
    font-size: 1.3rem;
    cursor: pointer;
    display: block;
    width: 100%;
    padding: 0.3rem 0;
    margin-bottom: 0; }
    .as-contact-form input[type="submit"]:hover {
      background-color: #eab426;
      margin-top: 4px;
      margin-bottom: -4px; }
    .as-contact-form input[type="submit"]:active {
      background-color: #e8af19;
      margin-top: 4px;
      margin-bottom: -4px; }
    .as-contact-form input[type="submit"]:hover, .as-contact-form input[type="submit"]:active {
      margin-bottom: 0; }

.recently-post {
  margin-top: 2.5rem; }
  .recently-post .section-title {
    font-weight: bold; }
  .recently-post .post-list {
    margin-left: 0;
    list-style: none; }
    .recently-post .post-list .post-item {
      height: 11rem;
      padding: 1rem 0;
      border-bottom: 1px solid #9CADB6; }
      .recently-post .post-list .post-item .post-image-container {
        position: relative;
        float: left;
        width: 10rem;
        height: 100%;
        margin-right: 1rem;
        overflow: hidden; }
        .recently-post .post-list .post-item .post-image-container .post-image {
          position: absolute;
          width: 100%; }
      .recently-post .post-list .post-item .post-content {
        line-height: 1.6;
        color: #999999; }

.social-icons {
  list-style: none;
