.single .thumbnail-container {
  height: 40vh;
  overflow: hidden; }
  .single .thumbnail-container .wp-post-image {
    transform: translateY(-25%); }
.single .main-container {
  margin-top: 2rem; }
  .single .main-container .entry-title {
    font-weight: bold; }
  .single .main-container .entry-content > p {
    font-size: 1.2rem;
    line-height: 2; }
  .single .main-container .entry-meta {
    margin: 2rem 0; }
    .single .main-container .entry-meta .author-card {
      padding: 1rem 1.5rem;
      border-radius: 8px;
      background-color: #EDBF47;
      box-shadow: #bdc3c7 0 5px 5px; }
      .single .main-container .entry-meta .author-card .avatar {
        float: left;
